@tailwind base;
@tailwind components;
@tailwind utilities;

.scroll-hide {
    overflow-x: scroll; /* Enables horizontal scrolling */
    scrollbar-width: 5px; /* For Firefox */
    scrollbar-width: thin;         /* Options: auto, thin, none */
    scrollbar-color: #888 #f0f0f0; /* thumb color and track color */
}
/* For WebKit browsers */
.scroll-hide::-webkit-scrollbar {
    width: 5px;               /* Width of the scrollbar */
    height: 5px;              /* Height for horizontal scrollbar */
}

.scroll-hide::-webkit-scrollbar-track {
    background-color: #f0f0f0; /* Background of the track */
}

.scroll-hide::-webkit-scrollbar-thumb {
    background-color: #888;    /* Color of the thumb (the draggable part) */
    border-radius: 10px;       /* Rounded corners on the thumb */
    border: 2px solid #f0f0f0; /* Border around the thumb */
}

.scroll-hide::-webkit-scrollbar-thumb:hover {
    background-color: #555;    /* Thumb color when hovered */
}
